.header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px 20px;
    background-color: #333;
    color: white;
}

.logo {
    font-size: 32px;
    font-weight: bold;
}

.navList {
    list-style: none;
    display: flex;
    gap: 20px;
}

.navList a {
    color: inherit;
    text-decoration: none;
}
